/* Parametrs order:
1. font-size
2. line-height
3. font-family
4. letter-spacing
5. color
6. font-weight
*/
.h1 {
  font-size: 150px;
  line-height: 150px;
  font-family: "Bellefair", sans-serif;
  letter-spacing: 0;
  color: #FFFFFF;
  font-weight: 400;
}

.h2 {
  font-size: 100px;
  line-height: 100px;
  font-family: "Bellefair", sans-serif;
  letter-spacing: 0;
  color: #FFFFFF;
  font-weight: 400;
}

.h3 {
  font-size: 56px;
  line-height: 56px;
  font-family: "Bellefair", sans-serif;
  letter-spacing: 0;
  color: #FFFFFF;
  font-weight: 400;
}

.h4 {
  font-size: 32px;
  line-height: 32px;
  font-family: "Bellefair", sans-serif;
  letter-spacing: 0;
  color: #FFFFFF;
  font-weight: 400;
}

.h5 {
  font-size: 28px;
  line-height: 28px;
  font-family: "Barlow", sans-serif;
  letter-spacing: 4.75px;
  color: #FFFFFF;
  font-weight: 400;
}

.h5--lavander {
  color: #D0D6F9;
}

.h5--font-condensed {
  font-family: "Barlow Condensed", sans-serif;
}

.subheading1 {
  font-size: 28px;
  line-height: 28px;
  font-family: "Bellefair", sans-serif;
  letter-spacing: 0;
  color: #FFFFFF;
  font-weight: 400;
}

.subheading2 {
  font-size: 28px;
  line-height: 28px;
  font-family: "Barlow", sans-serif;
  letter-spacing: 2.35px;
  color: #FFFFFF;
  font-weight: 400;
}

.navText {
  font-size: 16px;
  line-height: 16px;
  font-family: "Barlow", sans-serif;
  letter-spacing: 2.7px;
  color: #FFFFFF;
  font-weight: 400;
}

*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  background-color: #0B0D17;
  height: 100vh;
}

.container {
  position: relative;
  z-index: 0;
  margin: 0 auto;
  min-width: 375px;
  max-width: 767px;
  width: 100%;
  min-height: 100%;
  font-size: 16px;
  line-height: 20px;
  font-family: "Barlow", sans-serif;
  letter-spacing: 0;
  color: #FFFFFF;
  font-weight: 400;
}
@media (min-width: 768px) {
  .container {
    font-size: 18px;
    line-height: 32px;
    font-family: "Barlow", sans-serif;
    letter-spacing: 0;
    color: #FFFFFF;
    font-weight: 400;
    min-width: 768px;
    max-width: 1439px;
    width: 100%;
  }
}
@media (min-width: 1440px) {
  .container {
    min-width: 1440px;
    max-width: 100vw;
    width: 100%;
  }
}

.container-background {
  position: absolute;
  background-color: #0B0D17;
  z-index: -100;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../../img/home/background-home-mobile.jpg");
}
@media (min-width: 768px) {
  .container-background {
    background-image: url("../../img/home/background-home-tablet.jpg");
  }
}
@media (min-width: 1440px) {
  .container-background {
    background-image: url("../../img/home/background-home-desktop.jpg");
  }
}

.page-main {
  padding: 0 24px;
  height: fit-content;
}
@media (min-width: 768px) {
  .page-main {
    padding: 0 39px;
  }
}
@media (min-width: 1440px) {
  .page-main {
    padding: 0 165px;
  }
}

.button {
  padding: 27px;
  width: 150px;
  height: 150px;
  background-color: #FFFFFF;
  border-radius: 50%;
  cursor: pointer;
  color: #0B0D17;
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: 1.25px;
  position: relative;
}
@media (min-width: 768px) {
  .button {
    margin-bottom: 90px;
  }
}
@media (min-width: 1440px) {
  .button {
    margin: 0;
  }
}
.button::before {
  content: "";
  position: absolute;
  width: 246px;
  height: 246px;
  border-radius: 50%;
  top: -48px;
  left: -48px;
  background-color: rgba(255, 255, 255, 0.1);
  transform: scale(0.2);
  transition: transform 0.7s;
}
.button:hover::before {
  background-color: rgba(255, 255, 255, 0.1);
  transform: scale(1);
}
@media (min-width: 768px) {
  .button {
    width: 242px;
    height: 242px;
    font-size: 32px;
    letter-spacing: 2px;
  }
  .button::before {
    top: 0px;
    left: 0px;
  }
  .button:hover::before {
    width: 418px;
    height: 418px;
    top: -88px;
    left: -88px;
  }
}
@media (min-width: 1440px) {
  .button {
    width: 274px;
    height: 274px;
  }
  .button:hover::before {
    width: 450px;
    height: 450px;
  }
}

.page-header {
  padding: 0 24px;
  padding-top: 24px;
  max-width: 100%;
  width: 100%;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .page-header {
    padding: 0;
    padding-left: 39px;
    padding-top: 0px;
    height: 96px;
    margin-bottom: 106px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding: 0;
    padding-left: 55px;
    padding-top: 40px;
    margin-bottom: 250px;
    height: 96px;
  }
}

.main-nav {
  gap: 0.8rem;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .main-nav {
    height: 100%;
    align-items: start;
  }
}
.main-nav__logo {
  display: flex;
  margin-right: auto;
}
@media (min-width: 768px) {
  .main-nav__logo {
    padding-top: 24px;
  }
}
@media (min-width: 1440px) {
  .main-nav__logo {
    padding-top: 28px;
  }
}
.main-nav__logo img {
  width: 40px;
  height: 40px;
  align-self: center;
}
@media (min-width: 768px) {
  .main-nav__logo img {
    width: 48px;
    height: 48px;
  }
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__wrapper {
  position: absolute;
  z-index: 10;
  right: 4px;
  top: 0;
  backdrop-filter: blur(70px);
  width: 254px;
  height: 100%;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: static;
    width: auto;
    background: none;
    padding-left: 0;
    padding-right: 0;
    display: flex;
    min-width: 450px;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    min-width: 830px;
    height: 96px;
    backdrop-filter: blur(20px);
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 37px;
    position: relative;
    height: 100%;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    justify-content: start;
    height: initial;
    gap: 38px;
  }
}
.main-nav__toogle {
  display: block;
  position: absolute;
  width: 19.09px;
  height: 19.09px;
  top: 32px;
  right: 30px;
  z-index: 100;
  border: 0;
  cursor: pointer;
  color: #D0D6F9;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-nav__toogle {
    display: none;
  }
}
.main-nav--opened .main-nav__toogle::before, .main-nav--opened .main-nav__toogle::after {
  content: "";
  position: absolute;
  top: 8px;
  right: 0;
  width: 24px;
  height: 3px;
  background-color: currentColor;
}
.main-nav--opened .main-nav__toogle::before {
  transform: translateY(-50%) rotate(45deg);
}
.main-nav--opened .main-nav__toogle::after {
  transform: translateY(-50%) rotate(-45deg);
}
.main-nav--closed .main-nav__toogle::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0;
  width: 24px;
  height: 3px;
  background-color: #D0D6F9;
  box-shadow: 0 8px 0 0 #D0D6F9, 0 16px 0 0 #D0D6F9;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    display: block;
    position: relative;
  }
  .main-nav--nojs .main-nav__toogle {
    display: none;
  }
}

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 32px;
  padding-top: 118px;
}
@media (min-width: 768px) {
  .site-list {
    padding: 0;
    background-color: rgba(255, 255, 255, 0.04);
    width: 100%;
    height: 100%;
  }
}
@media (min-width: 1440px) {
  .site-list {
    height: auto;
    padding-left: 123px;
  }
}
@media (min-width: 768px) {
  .site-list__item {
    height: 100%;
    padding-top: 30px;
  }
}
.site-list__item a {
  text-decoration: none;
  color: inherit;
  text-transform: uppercase;
  font-family: "Barlow Condensed";
  letter-spacing: 2.7px;
}
@media (min-width: 768px) {
  .site-list__item a {
    display: block;
    height: 100%;
    font-size: 14px;
    letter-spacing: 2.36px;
  }
}
@media (min-width: 1440px) {
  .site-list__item a {
    display: flex;
    font-size: 16px;
  }
}
@media (min-width: 768px) {
  .site-list__item .site-list__active-link {
    cursor: initial;
    border-bottom: 3px solid #FFFFFF;
  }
}
.site-list__item span {
  font-weight: 700;
  margin-right: 12px;
  letter-spacing: 2.7px;
}
@media (min-width: 768px) {
  .site-list__item span {
    display: none;
  }
}
@media (min-width: 1440px) {
  .site-list__item span {
    display: block;
    margin-right: 11px;
  }
}
.site-list__item a:not(.site-list__active-link):hover {
  opacity: 0.6;
}

.promo {
