  gap: 16px;
}
@media (min-width: 768px) {
  .promo__text {
    padding-bottom: 156px;
    gap: 24px;
  }
}
@media (min-width: 1440px) {
  .promo__text {
    padding-bottom: 88px;
  }
}
.promo__description {
  font-size: 15px;
  line-height: 25px;
  color: #D0D6F9;
  width: 327px;
}
@media (min-width: 768px) {
  .promo__description {
    font-size: 16px;
    line-height: 28px;
    width: 450px;
  }
}
@media (min-width: 1440px) {
  .promo__description {
    font-size: 18px;
    line-height: 32px;
    text-align: justify;
  }
}
.promo__text .h5 {
  font-size: 16px;
  letter-spacing: 2.7px;
}
@media (min-width: 768px) {
  .promo__text .h5 {
    font-size: 20px;
    letter-spacing: 3.38px;
  }
}
@media (min-width: 1440px) {
  .promo__text .h5 {
    align-self: flex-start;
